@font-face {
  font-display: swap;
  font-family: "Blrrpix";
  src: url("../app/fonts/blrrpix.woff2");
}
:root {
  --font-body: "Blrrpix";
  --transition-min: .2s;
  --transition-max: 8s;
  --animate-delay: 1s ;
}

:root {
  --white: hsl(0, 0%, 100%);
  --aqua: hsl(179, 100%, 50%);
  --cherry: hsl(327, 88%, 37%);
  --light-gray: hsl(0, 0%, 20%);
  --dark-gray: hsl(0, 0%, 8%);
  --black: hsl(0, 0%, 0%);
}

.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}

.delay-1 {
  animation-delay: var(--animate-delay);
}

.delay-2 {
  animation-delay: calc(var(--animate-delay) * 1.2);
}

.delay-3 {
  animation-delay: calc(var(--animate-delay) * 1.3);
}

.delay-4 {
  animation-delay: calc(var(--animate-delay) * 1.4);
}

.delay-5 {
  animation-delay: calc(var(--animate-delay) * 1.5);
}

.delay-6 {
  animation-delay: calc(var(--animate-delay) * 1.6);
}

.delay-7 {
  animation-delay: calc(var(--animate-delay) * 1.7);
}

.delay-8 {
  animation-delay: calc(var(--animate-delay) * 1.8);
}

.delay-9 {
  animation-delay: calc(var(--animate-delay) * 1.9);
}

.delay-10 {
  animation-delay: calc(var(--animate-delay) * 2);
}

.delay-11 {
  animation-delay: calc(var(--animate-delay) * 2.1);
}

.delay-12 {
  animation-delay: calc(var(--animate-delay) * 2.2);
}

.delay-13 {
  animation-delay: calc(var(--animate-delay) * 2.3);
}

.delay-14 {
  animation-delay: calc(var(--animate-delay) * 2.4);
}

.delay-15 {
  animation-delay: calc(var(--animate-delay) * 2.5);
}

.fadeIn {
  animation-name: fadeIn;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  outline: none;
}

body {
  margin: 0;
  line-height: 1.3;
  font-size: 1.5625rem;
  color: var(--white);
  font-family: var(--font-body);
  cursor: url("../images/tools/pointer.cur"), auto;
  background-image: url("../images/background.webp");
}
@media (min-width: 71.875em) {
  body {
    overflow: hidden;
  }
}

.title {
  display: inline-block;
  position: relative;
  font-size: 6.25rem;
  margin-bottom: 0.625rem;
  transform: translateX(0);
  cursor: url("../images/tools/alternate.cur"), auto;
}
.title:before, .title:after {
  content: "codlipa";
  position: absolute;
  left: 0;
  opacity: 0.8;
}
.title:after {
  color: var(--cherry);
  z-index: -2;
}
.title:before {
  color: var(--aqua);
  z-index: -1;
}
.title:hover:before, .title:hover:after {
  animation: glitch var(--transition-min) cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite;
}
.title:hover:after {
  animation-direction: reverse;
}

.subtitle {
  font-size: 1.875rem;
  margin-bottom: 0.625rem;
}
@media (min-width: 71.875em) {
  .subtitle {
    font-size: 1.875rem;
  }
}
.subtitle--gradient {
  background-image: linear-gradient(var(--cherry), var(--aqua));
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

.paragraph:not(:last-child) {
  margin-bottom: 1.5625rem;
}

h1, h2, h3, p {
  margin: 0;
}

a {
  color: var(--aqua);
  text-decoration: none;
  transition: var(--transition-min);
  text-shadow: 0 0 0.625rem var(--aqua);
  cursor: url("../images/tools/person.cur"), auto;
}
a:hover {
  color: var(--cherry);
  text-shadow: 0 0 0.625rem var(--cherry);
}

#click {
  cursor: url("../images/tools/link.cur"), auto;
}

.links--vertical {
  display: inline-flex;
  flex-direction: column;
}
.links a:not(.links--vertical) {
  color: var(--cherry);
  text-shadow: 0 0 0.625rem var(--cherry);
  cursor: url("../images/tools/link.cur"), auto;
}
.links a:not(.links--vertical):hover {
  color: var(--aqua);
  text-shadow: 0 0 0.625rem var(--aqua);
}
.links a:not(:last-child) {
  margin-right: 1.25rem;
}

#click {
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--black);
  position: fixed;
  z-index: 2;
}
#click::after {
  content: "";
  width: 37.5rem;
  min-height: 20.9375rem;
  background: url("../images/loading.webp") no-repeat 50%/contain;
}

.wrapper {
  height: 100vw;
}
@media (min-width: 71.875em) {
  .wrapper {
    transform: rotate(-90deg) translateX(-100vh);
    transform-origin: top left;
    overflow-x: scroll;
  }
  .wrapper::-webkit-scrollbar {
    display: none;
  }
  .wrapper__inner {
    display: flex;
    transform: rotate(90deg) translateY(-100vh);
    transform-origin: top left;
  }
}

.slide {
  display: flex;
  flex-direction: column;
}
.slide.one {
  justify-content: center;
  align-items: center;
}
.slide.two {
  align-items: end;
  background-image: none;
  background-color: var(--light-gray);
}
.slide::before {
  content: "";
  min-width: 100vw;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  position: absolute;
  min-width: 100%;
  overflow: hidden;
}
.container__portfolio {
  box-shadow: 0 0 2.5rem 2.8125rem var(--dark-gray);
  background-color: var(--dark-gray);
}
@media (min-width: 71.875em) {
  .container {
    flex-direction: row;
    align-items: center;
  }
  .container__portfolio {
    align-items: start;
    justify-content: end;
    min-height: 100vh;
  }
}

.scroll {
  overflow: visible;
}
@media (min-width: 71.875em) {
  .scroll {
    overflow: scroll;
    max-height: 100vh;
  }
}
.scroll:first-child {
  min-width: 100vw;
}
@media (min-width: 71.875em) {
  .scroll:first-child {
    max-width: 40rem;
    min-width: auto;
  }
}
.scroll:last-child {
  max-width: 71.25rem;
}
.scroll::-webkit-scrollbar {
  display: none;
}

.wb-body {
  background-color: var(--dark-gray);
  color: var(--white);
  padding: 20px;
}
.wb-body::-webkit-scrollbar {
  display: none;
}

.hidden {
  position: relative;
  left: -100%;
}

.one .section {
  text-align: center;
}
@media (min-width: 71.875em) {
  .one .section {
    text-align: left;
  }
}
.one .section .social {
  margin-top: 3.75rem;
  line-height: 1.6;
}
@media (min-width: 71.875em) {
  .one .section .social {
    margin-top: 3.75rem;
  }
}

@keyframes glitch {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translate(-0.3125rem, 0.3125rem);
  }
  40% {
    transform: translate(-0.3125rem, -0.3125rem);
  }
  60% {
    transform: translate(0.3125rem, 0.3125rem);
  }
  80% {
    transform: translate(0.3125rem, -0.3125rem);
  }
  100% {
    transform: translate(0);
  }
}
.progress__box {
  min-width: 80%;
}
.progress__bar, .progress__bar span {
  min-width: 100%;
  border-radius: 0.625rem;
}
.progress__bar {
  position: relative;
  margin: 1.25rem 0;
  min-height: 1.25rem;
  background-color: var(--light-gray);
}
.progress__bar span {
  position: absolute;
  min-height: 100%;
  transform: scaleX(0);
  transform-origin: left;
  background-color: var(--aqua);
  box-shadow: 0 0 0.625rem var(--aqua);
  animation: progress var(--transition-min) forwards;
}
.progress__bar.html span {
  min-width: 90%;
}
.progress__bar.css span {
  min-width: 85%;
}
.progress__bar.javascript span {
  min-width: 70%;
}
.progress__bar.tailwind span {
  min-width: 75%;
}
.progress__bar.bootstrap span {
  min-width: 65%;
}
.progress__bar.git span {
  min-width: 80%;
}

@keyframes progress {
  100% {
    transform: scaleX(1);
  }
}
.hero {
  display: none;
  position: relative;
  min-height: 100vh;
}
@media (min-width: 71.875em) {
  .hero {
    display: flex;
    align-items: end;
  }
}
.hero::before {
  content: "";
  position: relative;
  left: 0;
  bottom: -2.375rem;
  min-height: 24.5rem;
  min-width: 46.125rem;
  transform: rotate(0);
  background: url("../images/console.webp") no-repeat 50%/contain;
}
.hero::after {
  content: "";
  display: none;
  position: absolute;
  left: 24%;
  bottom: 15.3125rem;
  min-width: 31.25rem;
  min-height: 26.5625rem;
  transform: scaleX(-1);
  background: url("../images/avatar.webp") no-repeat 50%/contain;
}
@media (min-width: 71.875em) {
  .hero::after {
    display: block;
  }
}

.timeline {
  display: flex;
  margin: 0 auto;
  align-items: center;
  text-align: center;
  padding: 3.75rem 0 0;
  flex-direction: column;
  background-color: var(--dark-gray);
}
@media (min-width: 71.875em) {
  .timeline {
    display: grid;
    align-items: stretch;
    grid-template-columns: 1fr 0.1875rem 1fr;
    padding: 2.5rem 0;
    text-align: left;
  }
}
.timeline__component {
  margin: 0 1.5625rem;
}
.timeline__component--bg {
  padding: 1.5625rem;
  background: var(--light-gray);
  box-shadow: 0 0 0.3125rem var(--light-gray);
  margin-bottom: 3.75rem;
  border-radius: 0.625rem;
  max-width: 42.5rem;
}
@media (min-width: 71.875em) {
  .timeline__component--bg {
    margin-bottom: 1.5625rem;
  }
}
.timeline__middle, .timeline__point {
  background-color: var(--aqua);
  box-shadow: 0 0 0.625rem var(--aqua);
}
.timeline__middle {
  position: relative;
}
.timeline__point {
  display: none;
}
@media (min-width: 71.875em) {
  .timeline__point {
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 0.9375rem;
    height: 0.9375rem;
    border-radius: 50%;
  }
}
.timeline__point--bottom {
  top: initial;
  bottom: 0;
}
.timeline__date {
  display: none;
}
@media (min-width: 71.875em) {
  .timeline__date {
    display: block;
  }
}
.timeline__date--right, .timeline__date--bottom {
  margin-bottom: 0;
}
@media (min-width: 71.875em) {
  .timeline__date--right {
    text-align: right;
  }
}

.portfolio {
  display: flex;
  flex-wrap: wrap;
  align-content: start;
}
.portfolio__item {
  min-width: 100%;
  min-height: 15.625rem;
  border: 0.3125rem solid var(--dark-gray);
  overflow: hidden;
  flex-grow: 1;
}
@media (min-width: 40em) {
  .portfolio__item {
    min-width: 50%;
  }
}
@media (min-width: 71.875em) {
  .portfolio__item {
    min-width: 23.75rem;
  }
}
.portfolio__item.soon {
  display: none;
}
@media (min-width: 71.875em) {
  .portfolio__item.soon {
    display: block;
  }
}
.portfolio__link {
  display: flex;
  justify-content: center;
  cursor: url("../images/tools/pin.cur"), auto;
  align-items: center;
  position: relative;
  min-height: 100%;
}
.portfolio__link:not(.item-0)::after {
  content: "";
  opacity: 0;
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  transition: var(--transition-min);
  background-color: rgba(0, 0, 0, 0.6);
}
.portfolio__link, .portfolio__link:hover:not(.item-0) {
  color: var(--white);
  text-shadow: none;
}
.portfolio__link:hover:not(.item-0)::after, .portfolio__link:hover:not(.item-0) .portfolio__text {
  opacity: 1;
}
.portfolio__link:hover:not(.item-0) .portfolio__image {
  filter: blur(0.125rem);
  transform: scale(1.1);
}
.portfolio__link.item-0 {
  cursor: url("../images/tools/unavailiable.cur"), auto;
  background: url("https://via.placeholder.com/380x250?text=Coming+Soon") no-repeat 50%/cover;
}
.portfolio__image, .portfolio__text {
  transition: var(--transition-min);
}
.portfolio__image {
  position: absolute;
  max-width: 100%;
  min-height: 100%;
  min-width: 30.3125rem;
}
.portfolio__text {
  position: relative;
  opacity: 0;
  z-index: 1;
}